.footer{
  // display: none;
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  background-image: url(../images/footer-background-image.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 200px;

  @include on-tablet {
    height: 400px;
  }

  @include on-desktop {
    height: 460px;
  }
}
