.numbers{
  display: flex;
  align-items: center;
  gap: 32px;

  &-number{
    display: none;
    text-decoration: none;
    color: $main-text-color;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 2px;
    cursor: url(../icons/cursor-pointer.svg), default;

  }
  &:hover .numbers-number{
    @include on-desktop{
      display: block;
    }
  }
}

