.about-us {
  @include page-grid;

  &-top{
    margin-top: 248px;

    @include on-tablet{
      margin-top: 456px;
    }

    @include on-tablet{
      margin-top: 516px;
    }
  }

  &__title {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: span 3;
    }

    @include on-desktop {
      grid-column: span 6;
    }
  }

  &__text {
    font-family: 'Space Mono', sans-serif;
    font-size: 16px;
    font-weight: 400;

    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: span 3;
    }

    @include on-desktop {
      grid-column: span 6;
    }
  }
}
